@import url(https://fonts.googleapis.com/css?family=Quicksand);
:root {
  --font: 'Quicksand', sans-serif;

  --success-color: #13AFA0;
  --danger-color: #EA0E48;
  --neutral-color: #cacaca;
  --primary-color-on: #f4f4f4;
  --primary-color: #1e1d1a;

  --header-height: 4rem;
  --footer-height: 5rem;

  margin: 0;
  padding: 0;
  min-height: 100%;
  display: -webkit-flex;
  display: flex;
}

body {
  margin: 0;
  padding: 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  background-color: #1e1d1a;
  background-color: var(--primary-color);
  font-family: 'Quicksand', sans-serif;
  font-family: var(--font);
  color: white;
}

#root {
  margin: 0;
  padding: 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
}

* {
  box-sizing: border-box;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
  font-family: var(--font);
  font-weight: 400;
}

input, textarea {
  touch-action: manipulation;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h1, h2 {
  font-family: 'Quicksand', sans-serif;
  font-family: var(--font);
  font-weight: 400;
}

button {
  font-weight: 400;
}

.dashboard {
  width: 100%;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
[class^="orderListItem"] {
  margin-bottom: 0.5rem;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;
  height: 10rem;
  color: var(--primary-color-on);
  background: var(--primary-color);
  transition: 0.5s ease-in all;
}

.orderListItem--expanded {
  height: auto;
  transition: 0.5s ease-in all;
}

[class^="wrapper"] {
  -webkit-align-self: stretch;
          align-self: stretch;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  font-size: 2rem;
}

.wrapper-back {
  display: inline-block;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  background: var(--danger-color);
  color: var(--primary-color-on);
}

[class^="wrapper-list"] {
  position: relative;
  padding: 0.5rem 1rem;
  -webkit-flex: 4 0 30%;
          flex: 4 0 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  overflow-y: hidden;
}

.wrapper-list--expanded {
  padding-bottom: 2rem;
}

[class^="arrow-button"] {
  height: 4rem;
  width: 100%;
  position: absolute;
  bottom: 0;
  z-index: 2;
  background: linear-gradient(to top, #1e1d1aFF, rgba(30, 29, 26, 0.5) 70%, #1e1d1a00);
  transition: 0.5s ease-in all;
}

.arrow-button--expanded {
  background: none;
}

[class^="arrow-button"]::after {
  content: "";
  height: 2rem;
  width: 2rem;
  position: absolute;
  bottom: 0;
  left: 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  background-position: center;
  background-size: 100%;
}

.arrow-button--expanded::after {
  background-image: url(data:image/png;base64,);
}

.item {
  margin-bottom: 10px;
  color: var(--primary-color-on);
  display: block;
}

.wrapper-status {
  -webkit-flex: 2 0 10%;
          flex: 2 0 10%;
  background: var(--success-color);
  color: var(--primary-color-on);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.queue {
  padding-bottom: 30rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
:root {
  --font: 'Quicksand', sans-serif;
  --success-color: #13AFA0;
  --danger-color: #EA0E48;
  --neutral-color: #cacaca;
  --primary-color-on: #f4f4f4;
  --primary-color: #1e1d1a;
}


.display {
  display: -webkit-flex;
  display: flex;
  padding: 4rem;
}

.pickup {
  -webkit-flex-grow: 2;
          flex-grow: 2;
}

.right-side {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.last-delivered {
  margin-bottom: 2rem;
}
.qrcode {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
  background: white;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-bottom: 15rem;
}

.qrcode__url {
  color: black;
  font-size: 3rem;
  margin-bottom: 2rem;
}
.wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 6rem;
  width: 100%;
  border: none;
  outline: none;
  background: var(--secondary-color);
  z-index: 10;
}

.btn {
  width: 100%;
  height: 100%;
  padding: 2rem 0;
  font-size: 1rem;
  background-image: url(data:image/png;base64,);
  background-size: 4rem;
  background-position: center;
  background-repeat: no-repeat;
  background-color: none;
  outline: none;
  border: none;
}

.menu {
  width: 100%;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  background: none;
}

[class^="menu-item"] {
  width: 100%;
  display: block;
  text-align: center;
  font-size: 2rem;
  height: 4rem;
  text-align: center;
  background: var(--primary-color);
  color: var(--primary-color-on);
}

.menu-item--selected {
  background: var(--neutral-color);
  color: var(--primary-color);
}

.menu-item__block {
  background: var(--danger-color);
}

.menu-item__block--selected {
  background: var(--success-color);
}

.menu-item__logout {
  background: var(--danger-color);
}
.login__wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  line-height: 100px;
  color: var(--primary-color-on);
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
}


.label {
  font-size: 25px;
  line-height: 25px;
  color: var(--primary-color-on);
  padding-bottom: 25px;
}

.input {
  border-radius: 10px;
  padding: 10px 18px;
  margin-bottom: 25px;
}

.login-message {
  margin: 0;
  color: var(--danger-color);
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 25px;
}

.submit {
  background-color: var(--primary-color-on);
  border-radius: 18px;
  padding: 10px 20px;
  margin-bottom: 10px;
}

.App {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  font-family: var(--font);
}
